import React from 'react'

export const ProyectsScreen = () => {

    const handleInputClick = ( e ) => {
        const {name} = e.target;
        window.open(name);
    }


    return (
        <div id="proyects" className="proyects screen">
            <h1 className>Proyectos</h1>
            <div className="proyects-conteiner">
                <div className="proyects-item conteiner">
                    <h3 className="text">Basicamente un CandyCrush hecho en JavaScript que posee 5 niveles de dificultad. Uno de mis primeros proyectos 
                    en JS por lo que todavía no poseía mucha experiencia en códigos y por lo tanto está bastante desordenado. 
                    </h3>
                    <a
                    className="hover"
                    target="_blank"
                    href="https://alela11i.github.io/candyCrush/" 
                    >
                        CandyCrush
                    </a>
                    <a
                    className="last hover"
                    target="_blank"
                    href="https://github.com/aleLa11i/candyCrush.git" 
                    >
                        Codigo
                    </a>
                </div>

                <div className="proyects-item conteiner">
                    <h3 className="text"> Uno de mis primeros proyectos en React, lo diseñe en un curso de Udemy.
                        Es basicamente un buscador de Gifs donde por cada busqueda devuelve los 10 primeros Gifs encontrados. 
                        Utilice una API muy famosa para Gifs (utilizada por WhatsApp entre otras aplicaciones).  </h3>
                    <a
                    className="hover"
                    target="_blank"
                    href="https://alela11i.github.io/gifapp/" 
                    >
                        GifApp
                    </a>
                    <a
                    className="last hover"
                    target="_blank"
                    href="https://github.com/aleLa11i/gifapp.git" 
                    >
                        Codigo
                    </a>
                </div>

                <div className="proyects-item conteiner">
                    <h3 className="text">Otro de mis proyectos diseñado en el curso de React. TODOapp es basicamente una app que sirve para agregar tareas que debo realizar.
                        A medida que se van realizando se pueden clickear sobre ellas para tacharlas y/o borrarlas. No se requirió Backend. 
                        No es un programa complejo en cuanto a lineas de código pero se utilizaron varios Hooks.</h3>
                    <a
                    className="hover"
                    target="_blank"
                    href="https://alela11i.github.io/TODOapp/" 
                    >
                        TODOapp
                    </a>
                    <a
                    className="last hover"
                    target="_blank"
                    href="https://github.com/aleLa11i/TODOapp.git" 
                    >
                        Codigo
                    </a>
                </div>
                
                <div className="proyects-item conteiner">
                    <h3 className="text">Uno de mis primeros proyectos con React complementado con Backend. Utilizando Node JS para el backend y Mongo DB como Base de Datos.
                        El proyecto se basa en un calendario (con login validado por backend) donde permite crear eventos y guardarlos.
                        Puedes compartir los eventos con el resto de las personas que se logean en la app pero solo vos podes modificar
                        tus eventos. Al logearse se genera un token con duracion de 2hs que se renueva, lo que matiene la sesion
                        abierta dentro del navegador hasta que se de logout. App desplegada en heroku.
                    </h3>
                    <a
                    className="hover"
                    target="_blank"
                    href="https://mern-calendar-alelalli.herokuapp.com/" 
                    >
                        CalendarApp
                    </a>
                    <a
                        className="hover"
                        target="_blank"
                        href="https://github.com/aleLa11i/mern-calendar-frontend.git" 
                    >
                        Codigo frontend
                    </a>
                    <a
                        className="last hover"
                        target="_blank"
                        href="https://github.com/aleLa11i/mern-calendar-backend.git" 
                    >
                        Codigo backend
                    </a>
                </div>
            </div>
        </div>
    )
}
