import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faWindowClose } from '@fortawesome/free-solid-svg-icons';




export const NavBar = () => {

    const handleNavbarClick = () => {
        
        document.getElementById("navbar").classList.add("show-navbar");
        document.getElementById("navbar-icon").classList.add("visible");

    }
    const handleNavbarClose = () => {
        document.getElementById("navbar").classList.remove("show-navbar");
        document.getElementById("navbar-icon").classList.remove("visible");

    }

    return (
        <div className="navbar-conteiner">
            <nav id="navbar" className="navbar">

                <FontAwesomeIcon icon={faWindowClose} className="close" onClick={handleNavbarClose} style={{display:"none"}} />
            
                <a 
                    activeClassName="active"
                    className="nav-item hover nav-home" 
                    href="#home"
                    onClick={handleNavbarClose}
                    >
                    Inicio
                </a>

                <a
                    activeClassName="active" 
                    className="nav-item hover"
                    href="#aboutme"
                    onClick={handleNavbarClose}
                    > 
                    Sobre mi
                </a>

                <a
                    activeClassName="active"
                    className="nav-item hover" 
                    href="#skills"
                    onClick={handleNavbarClose}
                    > 
                    Habilidades
                </a>

                <a
                    activeClassName="active" 
                    className="nav-item hover"
                    href="#proyects"
                    onClick={handleNavbarClose}
                    > 
                    Proyectos
                </a>

                <a
                    activeClassName="active" 
                    className="nav-item hover nav-contact"
                    href="#contact"
                    onClick={handleNavbarClose}
                    > 
                    Contacteme
                </a>
            
            </nav> 

            <div id="navbar-icon" className="navbar-icon" >
                <img 
                    src="https://res.cloudinary.com/dxzcqv92g/image/upload/v1634078458/kjeycctakluagocmvjwi.png" 
                    alt="img-logo"
                />
                <FontAwesomeIcon icon={faBars} className="navbar-icon-image" onClick={ handleNavbarClick }/>
            </div>


        </div>
    )
}
